<template>
<div class=" row ">
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
        <input type="number" class="form-control" id="done"  placeholder="완료 횟수" v-model="state.qdone">
        <div class="form-text">내가 완료한 목표 횟수</div>
    </div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
        <input type="number" class="form-control" id="goal"  placeholder="총 횟수" v-model="state.qgoal">
        <div class="form-text">총 목표 횟수</div>
    </div>
</div> 
<div class="row">
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
        <input type="date" class="form-control" id="goal" v-model="state.date" >
        <div class="form-text">목표 완료일자 설정 </div>
    </div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
        <ListGoalName @setInput="goalid" />
        <div class="form-text">필수항목(*)</div>
    </div>
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
</div>
<div class="row">
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="mb-3 col-lg-4 col-md-12 col-sm-12" style="text-align: right;">
        <button class=" btn btn-primary" @click.prevent="calendarEntryMarker">저장하기</button>
    </div>
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
</div>
</template>
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router';
import ListGoalName from './SelectGoalList.vue';
export default {
    setup(){
        const router = useRouter();
        const state = reactive({
            goalid  :'',
            qdone   :'',
            qgoal   :'',
            date    :'',
            email: sessionStorage.getItem("email"),
            token: sessionStorage.getItem("TOKEN"),
        })
        const goalid = (data) => {
        console.log(data);
        state.goalid = data;
        }
        const calendarEntryMarker = async ()=>{
            console.log("calendar Entry maker is launched")
            if(state.qdone==''){alert("input qdone");return false}
            if(state.qgoal==''){alert("input qgoal");return false}
            if(state.date ==''){ alert("choose a date");return false}
            if(state.goalid ==''){alert("select goal id");return false }
            const url = "http://43.201.60.125/api/createCalendarEntry";
            const headers = {
                "Content-Type": "application/json",
                Authorization: state.token,
                "token": state.token };
            const body = {
                goalid  : state.goalid,
                email   : state.email,
                qdone   : state.qdone,
                qgoal   : state.qgoal,
                date    : state.date,
            }
            console.log("body.goalid"+body.goalid)
            console.log("body.qdone"+body.qdone)
            console.log("body.qgoal"+body.qgoal)
            console.log("body.date"+body.date)
            console.log("body.email"+body.email)

            const response = await axios.post(url, body, { headers });
            console.log("response.data"+response.data)
            if(response.status === 200){
                alert("Create Calendar entry successfully");router.push({ name: "plans" });
            } else{ alert('fail')}
        }
        return{goalid, calendarEntryMarker, state, }

    },
    components: { ListGoalName }
}


</script>
<style scoped>
input:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
</style>
  